<template>
  <div class="big">
    <div class="top">
      <van-nav-bar title="制作详情" left-text="返回" left-arrow @click-left="onClickLeft" />
    </div>
    <div class="content clearfix">
      <div class="img">
        <img :src="arr[0].pic" alt />
        <h1>{{arr[0].name}}</h1>
      </div>
      <p class="intro">{{arr[0].content}}</p>
      <div class="label">
        <ul class="clearfix">
          <li v-for="(item , index) in tag" :key="index" class="fl">{{item}}</li>
        </ul>
      </div>
    </div>
    <div class="icon">
      <span>
        用餐人数：
        <van-icon name="manager" />
        {{arr[0].peoplenum}}
      </span>
      <span>
        烹饪时间：
        <van-icon name="underway" />
        {{arr[0].cookingtime}}
      </span>
    </div>
    <div class="foodMaterial">
      <h3>食材</h3>
      <div class="clearfix" v-for="(item , index) in arr[0].material" :key="index">
        <span class="fl">{{item.mname}}</span>
        <span class="fr">{{item.amount}}</span>
      </div>
    </div>
    <div class="modus">
      <h3>做法</h3>
      <div v-for="(item , index) in arr[0].process" :key="index">
        <img :src="item.pic" alt />
        <div>
          <span class="pagination">
            <span class="left">{{index+1}}</span>
            <span class="right">{{arr[0].process.length}}</span>
          </span>
          <span>{{item.pcontent}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      tag: [],
      arr: []
    };
  },
  computed: {
    ...mapState(["products"])
  },
  created() {
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,

      //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
      duration: 200
    });
    this.arr.push(JSON.parse(sessionStorage.getItem("detail")));
    
    //将文本中的html标签去除
   this.arr[0].content= this.arr[0].content.replace(/<[^>]+>/g,'')
   for(let i = 0 ; i < this.arr[0].process.length;i++){
        this.arr[0].process[i].pcontent= this.arr[0].process[i].pcontent.replace(/<[^>]+>/g,'')
   }
    for (let i = 0; i < this.arr[0].tag.split(",").length; i++) {
      this.tag.push(this.arr[0].tag.split(",")[i]);
    }

    if (this.products.length == 0) {
      return;
    }
    sessionStorage.setItem("products", JSON.stringify(this.products));
  },
  methods: {
    ...mapMutations([""]),
    onClickLeft() {
      this.$router.go(-1);
    }
   
  }
};
</script>

<style lang="less" scoped>
.top {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 6;
}
.content {
  margin-top: 46px;

  .intro {
    max-height: 200px;
    overflow: auto;
    margin: 0;
    margin: 0 8px;
    font-size: 12px;
    color: #999;
  }
  .img {
    width: 100%;
    position: relative;
    img {
      width: 100%;
      height: 220px;
    }
    h1 {
      text-align: center;
    }
  }
  .label {
    margin-top: 20px;
    width: 350px;
    max-height: 190px;
    overflow: auto;
    ul {
      li {
        width: 80px;
        height: 23px;
        line-height: 23px;
        border: 1px solid #1989fa;
        color: #1989fa;
        text-align: center;
        margin-top: 15px;
        margin-left: 30px;
        border-radius: 25px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}

.icon {
  span {
    display: inline-block;
    width: 40%;
    margin-left: 45px;
    margin-top: 30px;
    color: #999;
    position: relative;
    font-size: 13px;
    /deep/ .van-icon {
      position: absolute;
      top: 3px;
      left: -20px;
    }
  }
}
.foodMaterial {
  margin-left: 30px;
  h3 {
    font-size: 20px;
  }
  span {
    margin: 8px 30px;
    font-size: 14px;
  }
  .fr {
    margin-right: 60px;
  }
}
.modus {
  margin-left: 30px;

  h3 {
    font-size: 20px;
  }
  div {
    img {
      width: 320px;
      height: 220px;
      border-radius: 10px;
    }
    div {
      margin: 15px 0;
      font-size: 15px;
      color: #666;
      .pagination {
        display: inline-block;
        width: 330px;
        .left {
          &::after {
            content: "/";
          }
          font-weight: bold;
          font-size: 20px;
          color: black;
        }
      }
      > span {
        margin-left: 5px;
        display: inline-block;
        width: 330px;
      }
    }
  }
}
</style>